<template>
    <div class="toast">
        <ul>
            <li @click="toast">1.toast弹框----中间</li>
            <li @click="loading">2.加载中loading</li>
            <li>
                <v-upload-img
                    v-model="imgList"
                    :max="maxImglength"
                    desc="这里可传入备注说明..."
                    :lazy-tag="true"
                    @handle-uploaded-status="handleUploadedStatus">
                </v-upload-img>
            </li>
        </ul>
    </div>
</template>

<script>
    import vUploadImg from '../vendor/v-upload-img';

    export default {
        name: 'toast',
        components: { vUploadImg },
        data () {
            return {
                maxImglength: 3,
                imgList: []
            };
        },
        mounted () {
            console.warn(document.querySelector('.txt').getClientRects());
        },
        methods: {
            toast () {
                this.$toast.center('弹框在中间');
            },
            loading () {
                this.$store.commit('OPEN_LOADING');
                setTimeout(() => {
                    this.$store.dispatch('CLOSE_LOADING');
                }, 3000);
            },
            handleUploadedStatus (val) {
                let self = this;
                self.uploaded = val;
            }
        }
    };
</script>

